<template>
    <div class="page-content">
        <!-- 추후 form component file로 분할-->
        <div class="form-wrapper">
            <form action="" @submit.prevent="onSubmitQuery">
                <!-- <div class="row">
                    <div class="form-group">
                        <flat-pickr v-model="fromDate" id="date_from" :config="config" placeholder="시작일" class="form-control"></flat-pickr>
                    </div>
                    <div class="form-group">
                        <flat-pickr v-model="toDate" id="date_to" :config="config" placeholder="종료일" class="form-control"></flat-pickr>
                    </div>
                    <button type="submit">Search</button>
                </div> -->
                <div class="d-grid lg:grid-cols-4 md:grid-cols-3 grid-cols-1 gap-3">
                    <div class="form-group">
                        <flat-pickr v-model="fromDate" id="date_from" :config="config" placeholder="시작일" class="form-control"></flat-pickr>
                    </div>
                    <div class="form-group">
                        <flat-pickr v-model="toDate" id="date_to" :config="config" placeholder="종료일" class="form-control"></flat-pickr>
                    </div>
                    <div>
                        <button type="submit">Search</button>
                    </div>
                </div>
            </form>
        </div>
        <!-- <result-tras :results='res' @openDet='openDetail'></result-tras> -->
        <div id="loader_1" v-show="loading"></div>
        <result-tras v-show="showResults" @authorized="setAuthorized"></result-tras>
    </div>
</template>
<script>
// 임시 데이터 json load
//import res from '@/assets/json/transactions.json'
//import axios from 'axios'

import api from '@/utils/api'
import { mapGetters } from 'vuex'
import ResultTras from './ResultTras.vue'
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/flatpickr.css'
import { Korean } from "flatpickr/dist/l10n/ko.js"

export default {
    name:'SearchTras',
    components: {
        ResultTras,
        flatPickr
    },
    data() {
        return {
            fromDate:null,
            toDate:null,
            config:{ locale: Korean, enableTime:false, dateFormat: "Y-m-d" },
            //res:''
            //showResults:false,
            loading:false,
        }
    },
    methods:{
        //lazyLoadJson:()=>import('@/assets/json/transaction_modified.json'),
        setPeriod: function(offsetY, offsetM, offsetD) {
            var enday = new Date(this.toDate)
            enday.setFullYear(enday.getFullYear() - offsetY)
            enday.setMonth(enday.getMonth() - offsetM)
            enday.setDate(enday.getDate() - offsetD)
            this.fromDate = enday
        },
        maskName: name=>{
            if (name.length <= 2) {
                return name.replace(name.substring(0, 1), "*");
            }

            return (
                name[0] +
                "*".repeat(name.substring(1, name.length - 1).length) +
                name[name.length - 1]
            );
        },
        // submit button(Search)
        onSubmitQuery: async function(){
            // axios로 데이터 로딩
            /*axios.get('transactions.json')
                .then(res=>{
                    console.log(res.data);
                    this.$store.dispatch('loadTransactions',res.data);
                })
                .catch(err=>{
                    alert('파일을 읽지 못했습니다');
                    console.log(err);
                })
            */
            // api.tras
            // show loader in loading data
            //this.showResults = false
            this.$store.commit('setResults', false)

            //document.getElementById('loader_1').style.display = 'block';
            this.loading = true

            try {
                let res = await api.tras(this.fromDate, this.toDate, '', 'approve')
                if(res.data.result!="denial") {
                    res.data.tras.forEach(tra=>{
                        tra.name_m = this.maskName(tra.payerName);
                    });
                }
                this.$store.dispatch('loadTransactions',res.data);

                //this.showResults = true
                this.$store.commit('setResults', true)
                this.loading = false
            } catch(error) {
                console.log(error.message);
                this.loading = false
            }

            // lazy loading 임시 데이터 json file - Promise를 기본으로 return해 주네?
            /*this.lazyLoadJson()
            .then(data=>{
                console.log(data);
                data.tras.forEach(tra=>{
                    tra.uname = this.maskName(tra.uname);
                });
                //this.res = data;
                this.$store.dispatch('loadTransactions',data);
            })
            .catch(err=>{
                console.log(err);
                alert('파일 읽기에 실패하였습니다');
            })*/
        },
        /*openDetail(item) {
            this.$emit('openDet',item);
        },*/
        setAuthorized(bool) {
            console.log('authorization state changed in searchTras', bool)
            this.$emit('authorized',bool)
        },
    },
    computed: {
        /*countsOfTras() {
            return this.results.length;
        },
        results() {
            return this.$store.getters.tras;
        }*/
        // 위 처리 모듈을 mapGetters로 통합
        ...mapGetters({
            countsOfTras:'getCounts',
            results:'tras',
        // 검색결과 테이블을 보여줄 지 여부
            showResults:'keepResults'
        }),
        reAuthorized() {
            return this.$parent.reAuthorized;
        },
    },
    mounted(){ 
        //this.$emit('viewTras',false); //console.log(this.res.count, this.res.tras)
        // 내역조회 UI가 열릴 때는 뷰모드를 search로
        this.$emit('viewMode','search')
        const now = new Date()
        this.toDate = now.getFullYear().toString() + '-' + ('0'+(now.getMonth()+1)).slice(-2) + '-' + ('0'+(now.getDate())).slice(-2)
        // this.setPeriod(0,0,1)
        this.fromDate = this.toDate
    },
}
</script>
<style scoped>
.form-wrapper { max-width:800px; }
.form-group label { margin-bottom:5px }
.form-group input { border:0; }

#loader_1 { margin:100px 0; position:relative; left:50%; top:50%; transform:translateX(-50%); border:6px solid var(--bg-white); border-top-color:var(--primary); border-radius:50%; width:60px; height:60px; -webkit-animation:spin 2s linear infinite; animation:spin 2s linear infinite; }
/* Safari */
@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); } 
}
</style>